import { observable } from 'mobx'
import autobind from 'mora-common/util/autobind'
import createIndexApi from './lib/createIndexApi'
import { Scroller } from './Scroller'

@autobind
export class IndexApp {
  api = createIndexApi(() => {}, () => {
    console.warn('需要登录')
  })

  @observable scroller = new Scroller()

  // for Test Page
  @observable settleWidth: boolean
  @observable inContainer: boolean

  toJSON() {
    return {}
  }
}

export interface IAppComponentProps {
  app?: IndexApp
}

export * from 'mobx'
export * from 'mobx-react'
export * from './Scroller'



