import * as React from 'react'
import autobind from 'mora-common/util/autobind'
import viewport from 'mora-common/dom/viewport'
import onview from 'mora-common/dom/onview'
import Loading from 'mora-common/widget/Loading'
import {IScrollerItemProps, IScrollerItem, Scroller} from 'mobx/Scroller'
import {observer} from 'mobx-react'

export interface IScrollerContainerProps {
  load: (done: () => void) => void
  children: IScrollerItem[]
  scroller: Scroller

  /** 距离屏幕的偏移量，用于判断元素是否在屏幕内 */
  offset?: number
  /** 指定要预先加载的末在屏幕内的元素的个数 */
  preload?: number

  /**
   * 默认情况下，当 resize 后，item 的高度有可能会变化，所以只要有 resize 发生，就会渲染出所有存在的 items
   * 如果可以确定 resize 后，item 的高度不会变化，可以将此值设置成 true
   */
  itemWidthUnchangable?: boolean

  container?: Element
  loading?: JSX.Element
  loaded?: JSX.Element
  style?: React.CSSProperties
}

@observer
export class ScrollerContainer extends React.PureComponent<IScrollerContainerProps, any> {
  static defaultProps = {
    offset: 200,
    preload: 2, // 前后各预留两个 item
    container: document.documentElement,
    loading: <Loading stroke='#999' width={20} height={20} style={{display: 'block', margin: '10px auto'}} />,
    loaded: <div style={{lineHeight: '40px', textAlign: 'center', color: '#999', fontSize: 12}}>没有更多了~~</div>
  }

  off: any
  scroller = this.props.scroller
  refContainer: HTMLDivElement

  private sentinel: HTMLSpanElement
  private isLoading: boolean

  @autobind check() {
    let {load, container} = this.props
    this.caculate() // load 完后都会执行下 check，从而计算当前的 endIndex 和 startIndex

    // container 如果是 html 的话，html 反而根本不在 window 视口内，不如用 window 来判断 visiable
    container = container === document.documentElement ? null : container
    if (this.scroller.hasMore && viewport.visiable(this.sentinel, {offset: 100, container}) && !this.isLoading) {
      this.isLoading = true
      load(() => {
        this.isLoading = false
        this.check()
      })
    }
  }

  componentDidMount() {
    let {props: {container}, scroller: {containerScrollTop}} = this
    setTimeout(() => {
      container.scrollTop = containerScrollTop
      // 移动端需要用 body
      if (container === document.documentElement) document.body.scrollTop = containerScrollTop
    }, 10) // 奇怪，要异步才 work
    this.init()
  }

  componentDidUpdate(prevProps: IScrollerContainerProps) {
    if (this.props.container !== prevProps.container) {
      this.destroy()
      this.init()
      setTimeout(() => this.caculate(), 0)
    } else if (this.props.itemWidthUnchangable !== prevProps.itemWidthUnchangable) {
      // 异步，要不能得到的 container width 和上一次可能一样
      setTimeout(() => this.caculate(), 0)
    }
  }

  componentWillUnmount() {
    this.destroy()
  }

  init() {
    this.off = onview((e) => {
      this.check()
    }, {throttle: 200, container: this.props.container})
  }

  destroy() {
    if (this.off) {
      this.off()
      this.off = null
    }
  }

  caculate() {
    let {scroller, props} = this
    let {container, offset, preload, children} = props
    let containerScrollTop = container.scrollTop
    let containerHeight = container.clientHeight
    let scrollerWidth = this.refContainer.clientWidth

    if (container === document.documentElement && containerScrollTop === 0) {
      containerScrollTop = document.body.scrollTop // 移动端使用的是 body，桌面端使用的是 html
    }

    scroller.widthChanged = scroller.width != null && scroller.width !== scrollerWidth
    scroller.width = scrollerWidth
    scroller.containerScrollTop = containerScrollTop // 记录其值，不引起 rerender，因为 scrollTop 不是 observable

    if (scroller.widthChanged) {
      scroller.heightMap = {}
      // resize 了，从开头显示到最近的那个 item
      return scroller.updateIndex(0, scroller.items.length)
    }

    let {heightMap} = scroller
    let allHeight = 0
    let startIndex
    let endIndex
    let noHeightStartIndex = children.findIndex(it => !heightMap[it.props.id])
    children.forEach((it, i) => {
      allHeight += heightMap[it.props.id] || 0
      if (allHeight >= containerScrollTop - offset && startIndex == null) startIndex = i
      if (allHeight >= containerScrollTop + containerHeight + offset && endIndex == null) endIndex = i // 应该不显示 endIndex 本身
    })

    if (startIndex == null) startIndex = 0
    if (endIndex == null || noHeightStartIndex >= 0) endIndex = children.length

    // 如果没有找到 startIndex 是 -1，这时也会转化成 0，即显示所有
    startIndex = Math.max(0, startIndex - preload)
    endIndex = Math.min(children.length, endIndex + preload)

    scroller.updateIndex(startIndex, endIndex)
  }

  getRenderInfo() {
    let {props: {children}, scroller} = this
    let {startIndex, endIndex, heightMap, widthChanged} = scroller

    // 宽度变了要触发 item 重新计算 height
    let prefix = widthChanged ? Math.random().toString() : ''

    // 给 ScrollerItem 注入属性 scroller
    let renderItems = (children.slice(startIndex, endIndex) as any).map(c => {
      let props: any = {scroller}
      if (prefix) props.key = prefix + c.props.id // 触发 reload
      return React.cloneElement(c, props)
    })
    return {
      paddingTop: sum(children.slice(0, startIndex), heightMap),
      paddingBottom: sum(children.slice(endIndex), heightMap),
      children: renderItems
    }
  }

  render() {
    let {loading, loaded, style} = this.props
    let {children, paddingTop, paddingBottom} = this.getRenderInfo()
    // console.log('container render', this.scroller.widthChanged)
    return (
      <div className='wInfiniteScroller' style={style}>
        <div className='scrollerContainer' ref={e => this.refContainer = e} style={{paddingTop, paddingBottom}}>
          {children}
        </div>
        <div style={{background: 'transparent', width: '100%', height: 1}} ref={e => this.sentinel = e} />
        {this.scroller.hasMore ? loading : loaded}
      </div>
    )
  }
}

function sum(items, heightMap) {
  return items.reduce((s, it) => {
    s += heightMap[it.props.id] || 0
    return s
  }, 0)
}

export class ScrollerItem extends React.PureComponent<IScrollerItemProps, any> {
  root: HTMLDivElement

  @autobind update() {
    let {root, props} = this
    let {scroller, id} = props as any
    if (root) {
      scroller.heightMap[id] = root.clientHeight
    }
  }

  componentDidMount() {
    this.update()
  }

  componentDidUpdate(prevProps, prevState) {
    // console.log('update item height')
    this.update()
  }

  render() {
    let {render, children} = this.props
    return <div className='scrollerItem' style={{overflow: 'hidden'}} ref={e => this.root = e}>
      {render ? render() : children}
    </div>
  }
}




