import createApi from 'helper/createApi'

export default function(inject, login) {
  return createApi({
    getArticleList: {path: '/blog', query: pageQuery()},
    getArticle:     {path: '/blog/:articleId'}
  }, {base: '/v1', inject, login})
}

function pageQuery(data: any = {}) {
  return {
    page: {alias: 'pageNum', value: 1},
    pageSize: {value: 10},
    ...data
  }
}



