import * as React from 'react'
import Page from 'mora-common/widget/Page'
import {HashRouter as Router} from 'react-router-dom'
import TransitionRoute from 'widget/TransitionRoute'
import {Detail, List} from './Test'
// import Home from './Home'
// import People from './People'
// import Spec from './Spec'
// import Story from './Story'
// import QA from './QA'
// import Reply from './Reply'

export default class App extends React.PureComponent<any, any> {
  router: Router

  items = [
    {exact: true, path: '/', component: List},
    {exact: true, path: '/detail', component: Detail},
    // {exact: true, path: '/', component: Home},
    // {exact: true, path: '/people/:id', component: People},
    // {exact: false, path: '/people/:id/spec', component: Spec},
    // {exact: true, path: '/people/:id/story/:storyId', component: Story},
    // {exact: true, path: '/people/:id/qa/:questionId', component: QA},
    // {exact: true, path: '/people/:id/qa/:questionId/reply/:replyId', component: Reply}
  ]

  componentDidMount() {
    let r: any = this.router
    sendPageView(r.history.location)
    r.history.listen((location) => {
      sendPageView(location)
      window.scrollTo(0, 0)
    })
  }

  render() {
    return (
      <Page name='App' title='北回归线'>
        <Router ref={r => this.router = r}>
          <TransitionRoute items={this.items} />
        </Router>
      </Page>
    )
  }
}

function sendPageView(location) {
  /* tslint:disable */
  var ga = (window as any).ga
  // 打点文档：         https://developers.google.com/analytics/devguides/collection/analyticsjs/events?hl=zh-cn
  // pageview 文档：   https://developers.google.com/analytics/devguides/collection/analyticsjs/single-page-applications?hl=zh-cn
  if (typeof ga !== 'undefined') {
    let page = location.pathname + location.search + location.hash
    ga('set', 'page', page)
    ga('send', 'pageview', page) // 第三个参数是可选的
  }
  /* tslint:enable */
}



