import * as React from 'react'
import { RouteComponentProps } from 'react-router'
import { Link } from 'react-router-dom'
import Page from 'mora-common/widget/Page'
import {ScrollerContainer, ScrollerItem} from 'widget/InfiniteScroller'

import {inject, IAppComponentProps, observer} from 'mobx/IndexApp'

import './styles/Test.scss'

@inject('app')
@observer
export class List extends React.PureComponent<RouteComponentProps<any> & IAppComponentProps, any> {
  container: HTMLDivElement
  load(done) {
    setTimeout(() => {
      let {scroller} = this.props.app
      let a = []
      let r = () => Math.round(50 + (Math.random() * 205)).toString(16) // 确保一定是两位数
      let from = (scroller.page - 1) * 10
      for (let i = from; i < from + 10; i++) {
        let text = i + '.' + '点我可以跳到详情页' + ' foo bar '.repeat(Math.round(2 + Math.random() * 12))
        let background = '#' + r() + r() + r()
        a.push(
          <ScrollerItem key={i} id={i}>
            <Link
              to={'/detail'}
              className='card'
              data-bg={background}
              style={{background}}
              children={text}
            />
          </ScrollerItem>
        )
      }
      scroller.addItems(a, from < 40)
      done()
    }, 500)
  }

  componentDidMount() {
    this.forceUpdate()
  }

  render() {
    let {settleWidth, inContainer} = this.props.app
    return (
      <Page name={settleWidth ? 'Test' : 'Test auto'} title='Infinite Scroller'>
        <div className='controls'>
          <div><label>容器显示: <input type='checkbox' checked={inContainer} onChange={() => this.props.app.inContainer = !inContainer} /></label></div>
          <div><label>固定宽度: <input type='checkbox' checked={settleWidth} onChange={() => this.props.app.settleWidth = !settleWidth} /></label></div>
          <div style={{color: '#999'}}>注意：宽度不固定需要你手动改变屏幕大小，比如旋转屏幕</div>
        </div>
        <div className={(inContainer ? ' pin' : '')} ref={e => this.container = e}>
          {this.container ? (
            <ScrollerContainer container={inContainer ? this.container : undefined} preload={1} itemWidthUnchangable={settleWidth} scroller={this.props.app.scroller} load={(done) => this.load(done)}>
              {this.props.app.scroller.items}
            </ScrollerContainer>
          ) : null}
        </div>
      </Page>
    )
  }
}

export class Detail extends React.PureComponent<RouteComponentProps<any>, any> {
  render() {
    return <Page name={'TestDetail'} title='Infinite Scroller Detail'>
      <a className='back' onClick={() => this.props.history.goBack()}> &lt;- Back </a>
      <div style={{lineHeight: '50px'}}>{'detail page '.repeat(200)}</div>
    </Page>
  }
}



